import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


const Home = () => import('../view/register/Home')
const User = () => import('../view/userUI/User')
const UserChat = () => import('../view/userUI/UserChat')
const UserList = () => import('../view/userUI/UserList')
const DeleteUser = () => import('../view/delete/DeleteUser')

const routes = [
  {
    path: '',
    // redirect重定向
    redirect: '/home'
  },
  {
    path: '/home',
    component: Home
  },
  {
    path: '/user',
    component: User,
    children: [
      {
        path: 'list',
        component: UserList
      },
      {
        path: 'chat',
        component: UserChat,
      },
      {
        path: 'delete',
        component: DeleteUser,
      }
    ]
  }
]
export default new Router({
  routes: routes,
  mode: 'history',
  linkActiveClass: 'active'
})



