import axios from 'axios';
import store from "../store";

export function request(config) {
  // console.log(typeof store.state.globalUrl)
  const instance = axios.create({
    baseURL: store.state.globalUrl,
    timeout: 5000,
  })

  instance.interceptors.response.use(res => {
    // console.log(res)
    // console.log(res.data.responseStatus, typeof res.data.responseStatus)
    if (res.data.responseStatus === 1){
      // console.log(res.data.responseContent)
      return res.data.responseContent
    }else{
      console.log('xxxxxxxxxxx');
      alert("用户已存在 请勿重复注册");
    }
  })

  return instance(config)
}

export function request2(config) {
  const instance = axios.create({
    baseURL: store.state.globalUrl,
    timeout: 5000,
  })

  return instance(config)
}



