<template>
  <div id="app">
    <div id = 'menu'  ref="moveBlock"  @click="move">
      <span>{{name}}</span>
    </div>
    <router-view/>
  </div>
</template>

<script>

import {request2} from "./network/request";

export default {
  name: 'App',
  data() {
    return {
      offleft: 0,
      name: '',
      oldMem: this.$store.state.appNotice
    }
  },
  watch: {
    '$store.state.appNotice'() {
      console.log('change----------')
      this.name = this.$store.state.appNotice
      this.move()
    },
    offleft(val) {
      let that = this
      if (val === 275) {
        setTimeout( ()=> {
          that.move2()
        },1000)
      }
    }
  },
  methods: {
    move() {
      let oMenu = this.$refs.moveBlock;
      let speed = -10;
      //1、每次启动定时器将上一次定时器关闭
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        //2、运动和停止分开
        if(oMenu.offsetLeft === 275){
          clearInterval(this.timer);
        }else{
          oMenu.style.left = oMenu.offsetLeft + speed + 'px';
          this.offleft = oMenu.offsetLeft
        }
      }, 30);
    },
    move2() {
      let oMenu = this.$refs.moveBlock;
      let speed = 10;
      //1、每次启动定时器将上一次定时器关闭
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        //2、运动和停止分开
        if(oMenu.offsetLeft === 375){
          clearInterval(this.timer);
        }else{
          oMenu.style.left = oMenu.offsetLeft + speed + 'px';
          this.offleft = oMenu.style.left
        }
      }, 30);
    },
  },
}
</script>

<style>
  @import "./assets/css/base.css";
  html,body,#app{
    height: 100%;
  }
  body{
    background-color: #ebebeb;
    font-family: -apple-system;
    font-family: "-apple-system", "Helvetica Neue", "Roboto", "Segoe UI", sans-serif;
  }

  #menu{
    width: 100px;
    height: 100px;
    background-color: black;
    position: absolute;
    left: 375px;
    top: 200px;
    color: white}
  #menu span{
    line-height: 20px;
    position: absolute;
    left: 40px;
    top: 30px}
</style>



